type RouterPath = {
    pathRegex: RegExp;
    render: (params: { goTo: (path: string) => void }) => HTMLElement;
};

const routes: RouterPath[] = [
    {
        pathRegex: /^\/$/,
        render: ({ goTo }) => document.createElement('welcome-page'),
    },
    {
        pathRegex: /^\/instructions$/,
        render: ({ goTo }) => document.createElement('instructions-page'),
    },
    {
        pathRegex: /^\/play$/,
        render: ({ goTo }) => document.createElement('play-page'),
    },
    {
        pathRegex: /^\/result$/,
        render: ({ goTo }) => document.createElement('result-page'),
    },
    {
        pathRegex: /^\/desafio-piedra-papel-o-tijera\/?$/, // Nueva ruta para el desaf√≠o
        render: ({ goTo }) => document.createElement('desafio-page'), // Aseg√∫rate de que este componente exista
    },
];

function goTo(path: string) {
    window.history.pushState({}, "", path);
    renderPath(path);
}

function renderPath(path: string): void {
    const route = routes.find((route) => route.pathRegex.test(path));

    if (route) {
        const app = document.getElementById("app");
        if (app) {
            app.innerHTML = "";
            app.appendChild(route.render({ goTo }));
        }
    } else {
        console.warn(`El path '${path}' no fue encontrado.`);
    }
}

function getCleanPathFromURL() {
    // Define el basePath que se usar√° en producci√≥n
    const basePath = '/desafio-piedra-papel-o-tijera';
    
    // Obtiene el pathname completo
    const fullPath = window.location.pathname;
  
    // Verifica si el pathname comienza con el basePath
    if (fullPath.startsWith(basePath)) {
      // Elimina el basePath del pathname
      return fullPath.replace(basePath, '') || '/'; // Devuelve '/' si queda vac√≠o
    }
  
    // Si no hay basePath, devuelve el pathname limpio
    return fullPath;
  }
  

// Declara el evento personalizado en el √°mbito global
declare global {
    interface DocumentEventMap {
        goTo: CustomEvent;
    }
}

interface GoToEvent extends Event {
    detail: {
        path: string;
    };
}

export function initRouter(): void {
    const initialPath = getCleanPathFromURL();
    renderPath(initialPath);

    // Escuchar el evento 'goTo' en el elemento 'app'
    const app = document.getElementById("app");
    if(app){
        app.addEventListener('goTo', (event: CustomEvent) => {
            const { path } = event.detail;
            goTo(path); // Llama a la funci√≥n goTo con la nueva ruta
        });
    }
}




  
